/* eslint-disable */
import * as d3 from "d3"
import {apiInfo, requestDataCountry, questionMarkSvg} from "./constant.js"
import $ from "jquery"
import * as Overview from "./overview.js"
let CTX = {
  yType: 'normal',
  xType: 'log',
  commonRadius: 8,
  highlightRadius: 7,
  normalRadius: 200,
  fadeRadius: 0,
  tickNumber: 4,
  width: 633,
  height: 847,
  tickWidth: 750-69-63,
  rate_dx: 12,
  rate_dy: 12,
  rate_time_dx: -55,
  rate_time_dy: -18,
  death_dx: 10,
  death_dy: 6,
  death_time_dx:-60,
  death_time_dy: 20
}

const cnUnit = ['\u4e00', '\u5341', '\u767e', '\u5343', '\u4e07', '\u5341\u4e07', '\u767e\u4e07', '\u5343\u4e07', '\u4ebf', '\u5341\u4ebf', '\u5343\u4ebf']
const TEST_MODE = true
let SVGHEIGHT = 1079+28+80
let SVGWIDTH = 750
const MARGIN = {
  top: 183+80,
  bottom: 0,
  left: 67,
  right: 48
}
const INTERVAL = 1200
const blocksize = 12
const smallblock = 4
const XMAX = 99999999
const RATE_YMAX = 0.5
const DEATH_YMAX = 10000000
const DEATH_YMIN = 1

let plotTitle = function () {
  let svg = CTX.title
  let canvas = svg.append('g')
  let gRect = canvas.append('g').attr('transform', `translate(${45}, ${60+8+5})`)
  gRect.append('rect').attr('height', 34).attr('width', 8).style('rx', 4).style('ry', 4).style('fill', '#21d8e6')
  let gTitle = canvas.append('g').attr('transform', `translate(${68}, ${53+50})`)
  gTitle.append('text')
    .classed('bigTitle', true)
    .text('疫情轨迹线')
  gTitle.append('g').attr('transform', `translate(${45-68}, ${103})`)
    .append('text')
    .classed('secondTitle', true)
    .text('新冠肺炎与其他重大传染病感染/死亡情况对比')
  let gSubtitle = canvas.append('g').attr('transform', `translate(${68}, ${113+33})`)
  let longText = gSubtitle.append('text').classed('subTitle', true).text(`截至 ${CTX.updateTime} 国家卫健委数据统计 `)
  let bounding = document.querySelector('.subTitle').getBBox()
  let boundingWidth = bounding.width
  let questionMark = canvas.append('g')
  .attr('transform', `translate(${45 + 23 + boundingWidth + 5},${50+66+12/2})`)
  questionMark
    .append('g')
    .html(questionMarkSvg)
  questionMark.append('rect')
    .attr('height', 27)
    .attr('width', 27)
    .style('opacity', 0)
    .style('cursor', 'pointer')
    .on('click', function(){
    d3.select('#data-description').style('display', 'block')
  })
}

let plotMainView = function (svg) {
  drawBasicElement()
  initScale()
  let xTag = 'confirm'
  let yTag = 'rate'
  initWorld(xTag, yTag)
  initCountry(xTag, yTag)
}


let parseAPI = function (data) {
  console.log(data)
  let updateTime = data['args']['rsp']['chinaTotalUpdateTime']
  CTX.updateTime = updateTime
  let history = data['args']['rsp']['chinaHistoryTotal']
  history.forEach((v, i) => {
    let month = v['day'].substring(0,2)
    let date = v['day'].substring(3,2)
    let timeStr = `2020年${Number(month)}月${date}日`
    v['rate'] = v['dead'] / v['confirm']
    v['time'] = timeStr
    v['death'] = v['dead']
    v['offset'] = i
  })
  CTX.tid = history.length - 1
  CTX.currentTimeIdx = history.length - 1
  return history
}

let plotAdditive = function () {
  let special = ['季节性流感 (美国)', '甲型H1N1流感', '麻疹']
  CTX.world.forEach(d => {
    if(special.indexOf(d['name-cn'])==-1) return
    let l = d['rate_dy'] + 2 * blocksize
    let x = CTX.xScale(d['confirm'])
    let y1 = CTX.yScale(d['rate']) - blocksize
    CTX.canvas.append('line')
      .classed('additive-line', true)
      .attr('x1', x)
      .attr('x2', x)
      .attr('y1', y1)
      .attr('y2', y1+l)
  })
}

let updateAdditive = function () {
  if(CTX.yTag == 'death') {
    CTX.canvas
    .selectAll('.additive-line')
    .style('opacity', 0)
    return
  }
  CTX.canvas
  .selectAll('.additive-line')
  .style('opacity', 0)
  .transition()
  .delay(INTERVAL/2)
  .duration(INTERVAL/2)
  .style('opacity', 1)
}

let getData = function() {
  console.log(apiInfo)
  return requestDataCountry(apiInfo['countryHistory']).then(data => {
    console.log(data, 'shot')
    let country = parseAPI(data)
    CTX.country = country
    CTX.world = Overview.overview
    CTX.xMax = d3.max(CTX.world, d => d['confirm'])
    CTX.yMax = d3.max(CTX.world, d => d['rate'])
  })
}

let updateScale = function() {
  CTX.gYscale.selectAll('g').remove()
  let yScale
  let waitingTime = INTERVAL/5
  if (CTX.yType == 'normal') {
    CTX.canvas.select('.time-label').attr('y', 80)
    yScale = d3.scaleLinear().domain([0, RATE_YMAX]).range([CTX.height, 0])
    CTX.gYscale.transition().duration(waitingTime).call(d3.axisLeft(yScale).ticks(CTX.tickNumber).tickFormat(d => `${d*100}%`))
  } else {
    CTX.canvas.select('.time-label').attr('y', CTX.height - 20)
    CTX.yTag = 'death'
    CTX.yMax =  d3.max(CTX.world, d => d['death'])
    yScale = d3.scaleLog().domain([DEATH_YMIN, DEATH_YMAX]).range([CTX.height, 0])
    CTX.gYscale.transition().ease(d3.easeExpIn).duration(waitingTime).call(d3.axisLeft(yScale).ticks(CTX.tickNumber).tickFormat(d => `${cnUnit[Math.log10(d)]}`))
  }
  CTX.yScale = yScale
  CTX.gYscale.selectAll('.tick line').classed('tick-line', true).attr('y2', 0).transition().ease(d3.easeExpIn).duration(waitingTime+1).attr('x2', CTX.tickWidth)
  $('.y-axis .tick').insertBefore($('.solidDomain'))
  CTX.gYscale.selectAll('.tick text')
    .classed('tick-text-mortality', true)
    .attr('dy', 8)
    .style('text-anchor', 'end')
    .transition().delay(waitingTime+1).duration(0)
    .attr('x', -4)
  CTX.gYscale.select('.domain').remove()
}

let updateWorld = function(xTag, yTag) {
  CTX.canvas.selectAll('.world')
    .transition()
    .ease(d3.easePoly)
    .duration(INTERVAL)
    .attr('transform', (d, i) => `translate(${CTX.xScale(d[xTag])}, ${CTX.yScale(d[yTag])})`)

  let texts = CTX.canvas
    .selectAll('.disease')
    .transition()
    .ease(d3.easePoly)
    .duration(INTERVAL)
    .attr('x', d => CTX.xScale(d[xTag]))
    .attr('y', d => CTX.yScale(d[yTag]))
  texts.select('.disease-note')
    .attr('dx', d => d[`${yTag}_dx`])
    .attr('dy', d => d[`${yTag}_dy`])
  texts.select('.time-note')
    .attr('dx', d => d[`${yTag}_dx`])
    .attr('dy', d => d[`${yTag}_time_dy`])

  updateAdditive()
}

let initCountry = function(xTag, yTag) {
  let lineGenerator = d3.line().x(d => CTX.xScale(d[xTag])).y(d => CTX.yScale(d[yTag]))
  let countryLine = createLines(CTX.country)
  CTX.canvas.append('g')
    .classed('line-layer', true)
    .selectAll('path')
    .data(countryLine)
    .enter()
    .append('path')
    .attr('d', d => {
      return lineGenerator(d)
    })
    .attr('class', d => `lines country-line pl_${d[1]['offset']}`)

  CTX.canvas.append('g')
    .classed('dot-layer', true)
    .selectAll('.country')
    .data(CTX.country)
    .enter()
    .append('rect')
    .attr('transform', (d, i) => `translate(${CTX.xScale(d[xTag])},${CTX.yScale(d[yTag])})`)
    .attr('class', (d, i) => `country p_${d['offset']}`)
    .attr('width', (d, i) => i == CTX.country.length - 1 ? blocksize : smallblock)
    .attr('height', (d, i) => i == CTX.country.length -1 ? blocksize : smallblock)
    .attr('x', (d, i) => i == CTX.country.length - 1 ? -blocksize / 2 : -smallblock / 2)
    .attr('y', (d, i) => i == CTX.country.length - 1 ? -blocksize / 2 : -smallblock / 2)
    .style('fill', '#de5e5b')
    .style('fill-opacity', (d, i) => i == CTX.country.length - 1 ? 0 : 1)
    .style('stroke', (d, i) => '#de5e5b')

  let tooltip = CTX.canvas.append('text')
    .classed('countryId', true)
    .classed('highlightElement', true)
    .attr('x', CTX.xScale(CTX.country[CTX.country.length - 1][xTag]))
    .attr('y', CTX.yScale(CTX.country[CTX.country.length - 1][yTag]))
  tooltip.append('tspan')
    .classed('disease-name', true)
    .attr('dx', d => CTX[`${yTag}_dx`])
    .attr('dy', d => CTX[`${yTag}_dy`])
    .text('新冠肺炎')
  tooltip.append('tspan')
    .classed('disease-period', true)
    .attr('dx', d => CTX[`${yTag}_time_dx`])
    .attr('dy', d => CTX[`${yTag}_time_dy`])
    .text('2019-')
}

let initScale = function () {
  let xScale, yScale
  CTX.gXscale.selectAll('g').remove()
  xScale = d3.scaleLog().domain([100, XMAX]).range([0, CTX.width])
  CTX.gXscale.call(d3.axisBottom(xScale).ticks(6).tickFormat(d => {
    if(Math.log10(d)%1) return ''
   return  `${cnUnit[Math.log10(d)]}`
}))
  CTX.gXscale.selectAll('.tick').each(function (d) {
    if(Math.log10(d)%1) d3.select(this).remove()
  })
  CTX.gXscale.selectAll('.tick').select('line').attr('y2', -CTX.height).classed('tick-line', true)
  CTX.gXscale.select('.domain').remove()
  CTX.gXscale.select('.tick line').style('stroke-dasharray', 0).style('stroke', 'black')
  CTX.gXscale.selectAll('.tick text').classed('tick-text-mortality', true)//.last().remove()
  CTX.xScale = xScale
  if (CTX.yType == 'normal') {
    yScale = d3.scaleLinear().domain([0, RATE_YMAX]).range([CTX.height, 0])
    CTX.gYscale.call(d3.axisLeft(yScale).ticks(CTX.tickNumber).tickFormat(d => `${d*100}%`))
  } else {
    CTX.yTag = 'death'
  //  let yMax = 10**(Math.ceil(Math.log10(CTX.yMax))+0.4)
    yScale = d3.scaleLog().domain([100, DEATH_YMAX]).range([CTX.height, 0])
    CTX.gYscale.call(d3.axisLeft(yScale).ticks(CTX.tickNumber).tickFormat(d => `${cnUnit[Math.log10(d)]}`))

  }
  CTX.yScale = yScale
  CTX.gYscale.append('line')
    .classed('solidDomain', true)
    .attr('y1', CTX.height)
    .attr('y2', CTX.height)
    .attr('x2', CTX.width)
    .style('stroke', 'black')
  CTX.gYscale.selectAll('.tick line').classed('tick-line', true).attr('x2', CTX.tickWidth)
  CTX.gYscale.selectAll('.tick text')
    .classed('tick-text-mortality', true)
    .style('text-anchor', 'end')
    .attr('x', -4)
    .attr('dy', 8)
  CTX.gYscale.select('.domain').remove()
}

let initWorld = function(xTag, yTag) {
  CTX.canvas.append('g')
    .classed('dot-layer', true)
    .selectAll('.world')
    .data(CTX.world)
    .enter()
    .append('rect')
    .attr('transform', d => `translate(${CTX.xScale(d[xTag])}, ${CTX.yScale(d[yTag])})`)
    .attr('x', d => - blocksize / 2)
    .attr('y', d => - blocksize / 2)
    .attr('height', blocksize)
    .attr('width', blocksize)
    .attr('class', (d, i) => `world w_${i}`)
  let texts = CTX.canvas.append('g')
    .classed('annotation', true)
    .selectAll('text')
    .data(CTX.world)
    .enter()
    .append('text')
    .attr('x', d => CTX.xScale(Number(d[xTag])))
    .attr('y', d => CTX.yScale(Number(d[yTag])))
    .attr('class', (d, i) => `disease w_${i}`)

  texts.append('tspan')
    .classed('disease-note', true)
    .attr('x', d => CTX.xScale(Number(d[xTag])))
    .attr('dx', d => d[`${yTag}_dx`])
    .attr('dy', d => d[`${yTag}_dy`])
    .text(d => d['name-cn'])
  texts.append('tspan')
    .classed('time-note', true)
    .attr('x', d => CTX.xScale(Number(d[xTag])))
    .attr('dx', d => d[`${yTag}_dx`])
    .attr('dy', d => d[`${yTag}_time_dy`])
    .text(d => d['time'])
  plotAdditive()
}

let updateCountry = function(xTag, yTag) {
  let lineGenerator = d3.line().x(d => CTX.xScale(d[xTag])).y(d => CTX.yScale(d[yTag]))
  let tooltip = CTX.canvas.select('.countryId')
    .attr('x', CTX.xScale(CTX.country[CTX.tid][xTag]))
    .attr('y', CTX.yScale(CTX.country[CTX.tid][yTag]))
  tooltip.select('.disease-name')
    .attr('dx', d => CTX[`${yTag}_dx`])
    .attr('dy', d => CTX[`${yTag}_dy`])
  tooltip.select('.disease-period')
  .attr('dx', d => CTX[`${yTag}_time_dx`])
  .attr('dy', d => CTX[`${yTag}_time_dy`])
  tooltip
    .style('opacity', 0)
    .transition()
    .delay(INTERVAL)
    .style('opacity', 1)
  CTX.canvas.selectAll('.lines')
    .attr('d', d => lineGenerator(d))
    .style('opacity', 0)
    .transition()
    .delay((d, i) => INTERVAL/2)
    .duration(INTERVAL/2)
    .style('opacity', 1)
  CTX.canvas
    .selectAll('.country')
    .attr('class', (d, i) => `country p_${d['offset']}`)
    .style('visibility', (d, i) => i > CTX.tid? 'hidden':'visible')
    .style('opacity', 1)
    .transition()
    .duration((d, i) => INTERVAL)
    .attr('transform', d => `translate(${CTX.xScale(d[xTag])}, ${CTX.yScale(d[yTag])})`)
}

let updateHighlight = function(idx) {
  CTX.tid = idx
  d3.selectAll('.country-line').style('opacity', 1)
  let tooltip = CTX.canvas.select('.countryId')
    .attr('x', Math.max(0,CTX.xScale(CTX.country[idx][CTX.xTag])))
    .attr('y', CTX.yScale(CTX.country[idx][CTX.yTag]))
  for (let i = idx + 1; i <= CTX.currentTimeIdx; i++) {
    d3.selectAll(`.p_${i}`).style('visibility', 'hidden')
      .attr('width', smallblock)
      .attr('height', smallblock)
      .style('fill-opacity', 1)
      .style('x', -smallblock/2)
      .style('y', -smallblock/2)
    d3.selectAll(`.pl_${i}`).style('visibility', 'hidden')
  }
  d3.selectAll(`.p_${idx}`)
    .style('visibility', 'visible')
    .attr('width', blocksize)
    .attr('height', blocksize)
    .style('fill-opacity', 0)
    .style('x', -blocksize/2)
    .style('y', -blocksize/2)

  d3.selectAll(`.pl_${idx}`).style('visibility', 'visible')
  for (let i = 0; i < idx; i++) {
    d3.selectAll(`.p_${i}`)
      .style('visibility', 'visible')
      .attr('width', smallblock).classed('highlightElement', false)
      .attr('height', smallblock)
      .style('fill-opacity', 1)
      .style('x', -smallblock/2)
      .style('y', -smallblock/2)
    d3.selectAll(`.pl_${i}`).style('visibility', 'visible')
  }

}

let switchScale = function () {
  let a = d3.select('.active-text')
  let b = d3.select('.inactive-text')
  let c = d3.select('.active-button')
  let d = d3.select('.inactive-button')
  a.classed('active-text', false).classed('inactive-text', true)
  b.classed('active-text', true).classed('inactive-text', false)
  c.classed('active-button', false).classed('inactive-button', true)
  d.classed('active-button', true).classed('inactive-button', false)
  CTX.yType = CTX.yType=="normal"?"log":"normal"
  updateScale()
  let xTag = 'confirm'
  let yTag = CTX.yType == 'normal' ? 'rate' : 'death'
  CTX.xTag = xTag
  CTX.yTag = yTag
  updateWorld(xTag, yTag)
  updateCountry(xTag, yTag)
}

let createYtoggle = function(canvas) {
  const buttonWidth = 105
  const buttonx = 85
  const buttony = 175+50+30
  const buttonHeight = 40
  const halfHeight = 28
  const r = 6
  let buttonG = CTX.svg.append('g').attr('transform', `translate(${buttonx}, ${buttony})`)
  let clip = buttonG.append('defs')
    .append('clipPath')
    .attr('id', 'clip-button')
    .append('rect')
    .attr('rx', r)
    .attr('ry', r)
    .attr('height', buttonHeight)
    .attr('width', buttonWidth * 2)
  buttonG.append('rect')
    .attr('x', 0)
    .attr('y', 0)
    .attr('rx', r)
    .attr('ry', r)
    .attr('height', buttonHeight)
    .attr('width', buttonWidth * 2)
    .style('stroke', '#888888')
    .style('stroke-width', 0.5)
    .style('fill-opacity', 0)
  let containerLeft = buttonG.append('rect')
    .attr('width', buttonWidth)
    .attr('height', buttonHeight)
    .classed('active-button toggleButton', true)
    .on('click', function() {
      switchScale()
    })
  let containerRight = buttonG.append('rect')
    .attr('x', buttonWidth)
    .attr('width', buttonWidth)
    .attr('height', buttonHeight)
    .classed('inactive-button toggleButton', true)
    .on('click', function() {
      switchScale()
    })
  buttonG.append('text')
    .classed('button-text active-text', true)
    .attr('x', buttonWidth / 2)
    .attr('y', halfHeight)
    .text('(粗)病死率')
    .on('click', function() {
      switchScale()
    })
  buttonG.append('text')
    .classed('button-text inactive-text', true)
    .attr('x', buttonWidth / 2 + buttonWidth)
    .attr('y', halfHeight)
    .text('病亡人数')
    .on('click', function() {
      switchScale()
    })
}

let drawBasicElement = function() {
  let height = CTX.height
  let width = CTX.width
  let clipPath = CTX.svg.append('defs')
    .append('clipPath')
    .attr('id', 'mainCanvasClip')
    .append('rect')
    .attr('height', CTX.height+20)
    .attr('width', CTX.width+40)
    .attr('x', 0)
    .attr('y', 0)
  CTX.canvas.style('clip-path', 'url(#mainCanvasClip)')
  let canvas = CTX.svg.append('g').attr('transform', `translate(${MARGIN.left}, ${MARGIN.top})`)
  let label = canvas.append('text')
    .attr('x', CTX.width)
    .attr('y', CTX.height + 30)
    .attr('dy', 20)
    .classed('axis-label', true)
  label.append('tspan').text('确诊').attr('dy', 13).attr('dx', 0)
  label.append('tspan').text('人数').attr('dy', 25).attr('dx', -40)
  createYtoggle(canvas)
  let about = CTX.svg.append('g')
    .attr('transform', `translate(${40}, ${1179})`)
  about.append('text')
    .classed('fig-annotation', true)
    .text('*本图表采用对数坐标系')
  about.append('text')
    .style('text-anchor', 'end')
    .attr('x', )
}

let createLines = function(data) {
  let lines = []
  for (let i = 0; i < data.length; i++) {
    if (i == data.length - 1) break;
    lines.push([data[i], data[i + 1]])
  }
  return lines
}

let setParameter = function(svgId) {
  let width = document.querySelector('body').clientWidth
  let height = width / SVGWIDTH *  SVGHEIGHT
  let svg = d3.select(`#${svgId}`).attr('width', width).attr('height', height).attr('viewBox', `0 0 ${SVGWIDTH} ${SVGHEIGHT}`)
  let canvas = svg.append('g').attr('transform',`translate(${MARGIN.left}, ${MARGIN.top})`)//.append('g').attr('transform', `translate(${CTX.margin.left}, ${CTX.margin.top})`)
  CTX.svg = svg
  CTX.title = svg.append('g')
  CTX.gXscale = canvas.append('g').classed('x-axis', true).attr('transform', `translate(0, ${CTX.height})`)
  CTX.gYscale = canvas.append('g').classed('y-axis', true)
  CTX.canvas = canvas.append('g').classed('scatter-view', true)
  CTX.xTag = 'confirm'
  CTX.yTag = 'rate'
}

let updateView = function(idx) {
  updateHighlight(idx)
}


let setGlobalFont = function () {
  let ua = navigator.userAgent.toLowerCase();
  //FONT
  let fontFamily = 'Roboto, SourceHanSansCN, “思源黑体”, Noto Sans SC'
  if (/\(i[^;]+;( U;)? CPU.+Mac OS X/gi.test(ua)) {
      fontFamily = 'PingFang SC'
  }
  d3.select('body').style('font-family', fontFamily)
}

let init = async function(data) {
  setGlobalFont()
  setParameter('worldComparison')
  await getData()
  plotTitle()
  plotMainView(CTX.svg)
}

d3.selection.prototype.last = function() {
  var last = this.size() - 1;
  return d3.select(this._groups[0][last]);
};

export {
  init,
  updateView
};
