<template>
  <div style="width: 100%; height: 100%;">
    <svg id="worldComparison" />

    <div id="total_mortality" style="margin-top: 6.67vw">
      <div class="title_mortality">全国新冠肺炎累计感染/病亡人数</div>
      <div class="legend" style="margin-top: 3vw">
        <div class="legend_icon diagnosis" />
        <span class="legend_text">感染人数</span>
        <div class="legend_icon death" />
        <span class="legend_text">病亡人数</span>
      </div>
      <div class="total_svg_div" style="width: 100%; height: 35vw; margin-top: 3vw;">
        <div class="y_label" style="margin-top: 1.8vw;">
          <div
            v-for="(label, index) in add_y_label"
            class="label_text"
            :style="`margin-bottom: 4vw; font-weight: ${label === 0 ? 600 : 400};`"
            :key="`${label}-${index}`"
          >{{num2text(label)}}</div>
        </div>
        <div class="life_svg_div_mortality">
          <div class="highlight_svg_div_mortality" style="pointer-events: none;">
            <svg width="100%" height="100%">
              <defs>
                <linearGradient id="orange_red" x1="100%" y1="0%" x2="100%" y2="100%">
                  <stop offset="0%" style="stop-color:rgb(0, 186, 209); stop-opacity: 0.05" />
                  <stop offset="100%" style="stop-color:rgb(0, 186, 209); stop-opacity: 0.25" />
                </linearGradient>
              </defs>
              <rect
                :x="hightlight_rect.x"
                :y="hightlight_rect.y"
                :width="hightlight_rect.width"
                :height="hightlight_rect.height"
                fill="url(#orange_red)"
              />
            </svg>
          </div>
          <div
            class="life_svg_div_mortality_big"
            :style="`height: 100%; width: ${dataLens / showLens * 100}%; overflow: visible;`"
          >
            <svg width="100%" height="100%" style="background: none;">
              <g id="horizontal_line">
                <path
                  v-for="(label, index) in add_y_label"
                  :key="'add_y_line' + index"
                  :style="`stroke-width: ${
                    bold_line_index_list.includes(index) ? 0.3 : 0.08
                  }vw; stroke: #6f6f6f;`"
                  :d="bold_line_index_list.includes(index) ? '' : `M0,${add_y_step * (0.5 + index)}h${add_svg_width}`"
                />
              </g>

              <!-- <g class="bar_chart">
                <g class="bar_char_diagnosis">
                  <rect
                    v-for="(data, index) in country_dataset"
                    class="diagnosis"
                    :key="`bar-rect-diagnosis-${index}`"
                    :width="add_x_step*1.4"
                    :height="data.total_diagnosis / add_y_label[0] * add_y_step * 2"
                    :x="add_x_begin + add_x_step * (2 * index + 0.3)"
                    :y="(1 - data.total_diagnosis / add_y_label[0]) * add_y_step * 2 + 0.5 * add_y_step"
                  />
                  <text
                    v-for="(data, index) in country_dataset"
                    v-show="index===select_date_id"
                    class="label_text"
                    :key="`bar-label-cure-${index}`"
                    :x="add_x_begin + add_x_step * (2 * index + 1)"
                    :y="(1 - data.total_diagnosis / add_y_label[0]) * add_y_step * 2 + 0.4 * add_y_step"
                  >{{num2text(data.total_diagnosis)}}</text>
                </g>
                <g id="bar_chart_death">
                  <rect
                    v-for="(data, index) in country_dataset"
                    class="death"
                    :key="`bar-rect-death-${index}`"
                    :width="add_x_step*1.4"
                    :height="data.total_death / add_y_label[5] * add_y_step * 2"
                    :x="add_x_begin + add_x_step * (2 * index + 0.3)"
                    :y="3.5 * add_y_step"
                  />
                  <text
                    v-for="(data, index) in country_dataset"
                    v-show="index===select_date_id"
                    class="label_text"
                    :key="`bar-label-death-${index}`"
                    :x="add_x_begin + add_x_step * (2 * index + 1)"
                    :y="data.total_death / add_y_label[5] * add_y_step * 2 + 3.85 * add_y_step"
                  >{{num2text(data.total_death)}}</text>
                </g>
              </g> -->
              <g class="bar_chart">
                <g class="bar_char_diagnosis">
                  <rect
                    v-for="(data, index) in country_dataset"
                    class="diagnosis"
                    :key="`bar-rect-diagnosis-${index}`"
                    :width="add_x_step*1.4"
                    :height="(Math.log10(data.total_diagnosis) / Math.log10(add_y_label[0]) * add_y_step * 2)"
                    :x="add_x_begin + add_x_step * (2 * index + 0.3)"
                    :y="(1 - Math.log10(data.total_diagnosis) / Math.log10(add_y_label[0])) * add_y_step * 2 + 0.5 * add_y_step"
                  />
                  <text
                    v-for="(data, index) in country_dataset"
                    v-show="index===select_date_id"
                    class="label_text"
                    :key="`bar-label-cure-${index}`"
                    :x="add_x_begin + add_x_step * (2 * index + 1)"
                    :y="(1 - Math.log10(data.total_diagnosis) / Math.log10(add_y_label[0])) * add_y_step * 2 + 0.4 * add_y_step"
                  >{{num2text(data.total_diagnosis)}}</text>
                </g>
                <g id="bar_chart_death">
                  <rect
                    v-for="(data, index) in country_dataset"
                    class="death"
                    :key="`bar-rect-death-${index}`"
                    :width="add_x_step*1.4"
                    :height="Math.log10(data.total_death) / Math.log10(add_y_label[4]) * add_y_step * 1"
                    :x="add_x_begin + add_x_step * (2 * index + 0.3)"
                    :y="3.5 * add_y_step"
                  />
                  <text
                    v-for="(data, index) in country_dataset"
                    v-show="index===select_date_id"
                    class="label_text"
                    :key="`bar-label-death-${index}`"
                    :x="add_x_begin + add_x_step * (2 * index + 1)"
                    :y="Math.log10(data.total_death) / Math.log10(add_y_label[4]) * add_y_step * 1 + 3.85 * add_y_step"
                  >{{num2text(data.total_death)}}</text>
                </g>
              </g>

              <g id="x-label">
                <text
                  v-for="(date, index) in date_list"
                  v-show="index===select_date_id"
                  :style="index===select_date_id ? `text-anchor: middle; font-size: 2.4vw; font-weight: 500; fill: '#333333'; transform: translate(0, 0.1vw); transition: ease 0.2s;` : `text-anchor: middle; font-size: 2.1333333vw; font-weight: 400; fill: '#333333';`"
                  :x="add_x_begin + add_x_step * (2 * index + 1)"
                  :y="3.1 * add_y_step"
                  :key="`${date.month}-${date.day}`"
                >{{`${date.month}/${date.day}`}}</text>
              </g>
              <g id="horizontal_line_0">
                <path
                  v-for="(label, index) in add_y_label"
                  :key="'add_y_line' + index"
                  :style="`stroke-width: ${
                    bold_line_index_list.includes(index) ? 0.3 : 0.08
                  }vw; stroke: #6f6f6f;`"
                  :d="bold_line_index_list.includes(index) ? `M0,${add_y_step * (0.5 + index)}h${add_svg_width}` : ''"
                />
              </g>
            </svg>
          </div>
        </div>
      </div>
      <div id="slider_mortality">
        <svg id="slider_svg2" width="100%" height="100%">
          <defs>
            <linearGradient id="slider_linear">
              <stop offset="15%" style="stop-color:#60cad7; stop-opacity: 1" />
              <stop offset="95%" style="stop-color:#00bad1; stop-opacity: 1" />
            </linearGradient>
            <filter id="shadow" x="-5%" y="0%" width="110%" height="120%">
              <feDropShadow
                dx="0"
                dy="0.6"
                stdDeviation="1.2"
                flood-color="#00B9D1"
                flood-opacity="0.6"
              />
            </filter>
          </defs>
        </svg>
      </div>

      <div class="description" style="margin-bottom: 3.472vh;">*拖动图表查看更多时间范围数据，移动滑块选择日期，图表将同步变化</div>
    </div>
    <div id="data-description">
      <div class="card">
        <div class="card-title">
          <h1>数据说明</h1>
        </div>
        <div class="card-detail">
          <p>新冠肺炎数据来源于国家卫健委。由于当前疫情尚未结束，粗病死率是当日卫健委公布的因感染新型冠状病毒死亡的病例数量占累计报告确诊病例的比例；其他历史疫情为根据文献计算所得病死率。</p>
          <p>关于其他重大历史疾病的数据来源请访问北京大学可视化与可视分析实验室疫情轨迹线主页查看。</p>
        </div>
        <div class="card-line"></div>
        <div class="card-close">关闭</div>
      </div>
    </div>
  </div>
</template>

<script>
import * as ScatterPlot from "../assets/js/scatter.js";
import * as d3 from "d3";
export default {
  name: "Mortalityline",
  props: {
    msg: String
  },
  data() {
    return {
      ScatterPlot,
      country_dataset: [],
      date_list: [],
      add_y_label: [],
      add_y_step: 0,
      add_x_begin: 0,
      add_x_step: 0,
      add_x_end: 0,
      dataLens: 0,
      showLens: 10,
      hightlight_rect: {
        x: 0,
        y: 0,
        width: 100,
        height: 100
      },
      bold_line_index_list: [2, 3],
      select_date_id_slider: 0,
      select_date_id_bar: 0,
      select_date_id: 0,
      isiOS: false,
      dataViewWidth: 0,
      max_scroll_distance: 0,
    };
  },
  watch: {
    select_date_id_slider() {
      if (d3.select(".highlight_svg_div_mortality").node()) {
        let highlight_div_rect = d3
          .select(".highlight_svg_div_mortality")
          .node()
          .getBoundingClientRect();
        let svg_width = highlight_div_rect.width;
        let width = this.add_x_step * 2.6;
        let height = 8.5 * this.add_y_step;
        let x =
          svg_width -
          this.add_x_end -
          width / 2 -
          (this.showLens - 1 - this.select_date_id_slider) *
            2 *
            this.add_x_step -
          this.add_x_step * 1;
        let y = this.add_y_step * 0.5;
        this.hightlight_rect = {
          x,
          y,
          width,
          height
        };
      }
    },
    select_date_id(idx) {
      this.ScatterPlot.updateView(idx);
    }
  },
  methods: {
    set_slider() {
      let that = this;
      let DRAGWIDTH = d3
        .select("#slider_mortality")
        .node()
        .getBoundingClientRect().width;
      let DRAGHEIGHT = d3
        .select("#slider_mortality")
        .node()
        .getBoundingClientRect().height;
      let MARGIN = {
        left: 0.03 * DRAGWIDTH,
        right: 0.05 * DRAGWIDTH,
        top: 0,
        bottom: 0
      };
      let plotDragBars = function(svgId, total) {
        let svg = d3.select(`#${svgId}`);
        svg.attr("height", DRAGHEIGHT).attr("width", DRAGWIDTH);
        let canvas = svg
          .append("g")
          .attr("transform", `translate(${MARGIN.left}, ${MARGIN.top})`);
        let width = DRAGWIDTH - MARGIN.left - MARGIN.right;
        let height = DRAGHEIGHT - MARGIN.top - MARGIN.bottom;
        // let width = total_svg_div_rect.width;
        // let height = 30;
        let w = width / total;
        let h = height * 0.8;
        let slider_w = w * 4;
        let timeScale = d3
          .scaleLinear()
          .range([-slider_w * 0.5 + 0.5 * w, width - w * 0.5 - slider_w * 0.5])
          .domain([0, total - 1]);
        let g = canvas.append("g");
        g.append("line")
          .attr("x1", -width * 0.02)
          .attr("x2", width + 0.22 * w)
          .attr("y1", height * 0.5)
          .attr("y2", height * 0.5)
          .style("stroke", "#ececec")
          .style("stroke-width", height * 0.2)
          .style("stroke-linecap", "round");
        // .classed("slider-container", true);
        let bar_g = g.append("g");
        let bars = [0, 1, 2];
        bar_g
          .append("rect")
          .attr("x", w * total - w * 0.5 - slider_w * 0.5)
          .attr("y", height / 2 - h * 0.25)
          .attr("width", slider_w)
          .attr("height", h * 0.75)
          .attr("class", "bar_g_mortality")
          .attr("rx", 0.1 * slider_w)
          .attr("ry", 0.1 * slider_w)
          .classed("slider-rect-shadow", true);
        let target = g
          .append("rect")
          .attr("x", w * total - w * 0.5 - slider_w * 0.5)
          .attr("y", height / 2 - h / 2)
          .attr("width", slider_w)
          .attr("height", h)
          .attr("rx", 0.1 * slider_w)
          .attr("ry", 0.1 * slider_w)
          .classed("slider-rect", true);
        g.append("g")
          .selectAll("rect")
          .data(bars)
          .enter()
          .append("rect")
          .attr("class", "bar_g_mortality")
          .attr("x", (d, i) => {
            return w * total - w * 0.5 - slider_w * 0.5 + slider_w * 0.28 + i * 0.20 * slider_w;
          })
          .attr("y", height / 2 - h * 0.23)
          .attr("width", 0.04 * slider_w)
          .attr("height", 0.46 * h)
          .style("fill", "#ffffff")
          .attr("rx", 0.02 * w)
          .attr("ry", 0.02 * w);
        target.call(
          d3
            .drag()
            .on("drag", function() {
              let x = Math.min(Math.max(-slider_w * 0.5 + 0.5 * w, d3.event.x), width - w * 0.5 - slider_w * 0.5);
              let self = d3.select(this);
              self.attr("x", x);
              let timeIdx = Math.round(timeScale.invert(x));
              let position = timeIdx * w - slider_w * 0.5 + 0.5 * w;
              self.attr("x", position);
              d3.selectAll(".bar_g_mortality").attr(
                "transform",
                `translate(${(timeIdx - total + 1) * w}, 0)`
              );
              that.select_date_id_slider = timeIdx;
              that.select_date_id =
                that.select_date_id_slider + that.select_date_id_bar;
            })
            .on("end", function() {})
        );
      };
      this.select_date_id_slider = this.showLens - 1;
      let svgId = "slider_svg2";
      plotDragBars(svgId, this.showLens);
      this.select_date_id_bar = this.dataLens - this.showLens;
    },
    set_scroll_by_addEvent() {
      // console.log("set_scroll_by_addEvent Begin!!!!");
      let that = this;
      let timer = null;
      document
        .querySelector(".life_svg_div_mortality")
        .addEventListener("scroll", function(e) {
          // console.log("==========", e, e.target, e.target.scrollLeft);
          // console.log(e.target.scrollWidth, e.target.clientWidth);
          if (!that.isiOS) {
            that.scroll_left = e.target.scrollLeft;
            // console.log(timer)
            clearTimeout(timer);
            let tmp = that.scroll_left;
            that.max_scroll_distance = -that.dataViewWidth + that.add_svg_width;
            // console.log('that.max_scroll_distance', that.max_scroll_distance)
            // that.gap = that.max_scroll_distance - tmp;
            that.gap = Math.min(
              that.max_scroll_distance,
              Math.max(that.max_scroll_distance - tmp, 0)
            );
            let t = Math.round(that.gap / (that.add_x_step * 2));
            // console.log(t)
            that.select_date_id_bar = that.dataLens - that.showLens - t;
            that.select_date_id =
              that.select_date_id_bar + that.select_date_id_slider;
            timer = setTimeout(
              tmp => {
                // console.log(tmp);
                if (tmp === that.scroll_left) {
                  that.max_scroll_distance =
                    -that.dataViewWidth + that.add_svg_width;
                  // that.gap = that.max_scroll_distance - tmp;
                  that.gap = Math.min(
                    that.max_scroll_distance,
                    Math.max(that.max_scroll_distance - tmp, 0)
                  );
                  let t = Math.round(that.gap / (that.add_x_step * 2));
                  // that.select_date_id_bar = that.dataLens - that.showLens - t
                  // that.select_date_id =  that.select_date_id_bar + that.select_date_id_slider
                  let fix_tmp =
                    that.max_scroll_distance - t * (that.add_x_step * 2);
                  e.target.scrollLeft = fix_tmp;
                }
              },
              100,
              tmp
            );
          }
          if (that.isiOS) {
            that.scroll_left =
              e.target.scrollLeft + e.target.scrollWidth - e.target.clientWidth;
            // console.log(timer)
            clearTimeout(timer);
            let tmp = that.scroll_left;
            that.max_scroll_distance = -that.dataViewWidth + that.add_svg_width;
            // console.log('that.max_scroll_distance', that.max_scroll_distance)
            // that.gap = that.max_scroll_distance - tmp;
            that.gap = Math.min(
              that.max_scroll_distance,
              Math.max(that.max_scroll_distance - tmp, 0)
            );
            let t = Math.round(that.gap / (that.add_x_step * 2));
            // console.log(t)
            that.select_date_id_bar = that.dataLens - that.showLens - t;
            that.select_date_id =
              that.select_date_id_bar + that.select_date_id_slider;
            timer = setTimeout(
              tmp => {
                // console.log(tmp);
                if (tmp === that.scroll_left) {
                  that.max_scroll_distance =
                    -that.dataViewWidth + that.add_svg_width;
                  // that.gap = that.max_scroll_distance - tmp;
                  that.gap = Math.min(
                    that.max_scroll_distance,
                    Math.max(that.max_scroll_distance - tmp, 0)
                  );
                  let t = Math.round(that.gap / (that.add_x_step * 2));
                  // that.select_date_id_bar = that.dataLens - that.showLens - t
                  // that.select_date_id =  that.select_date_id_bar + that.select_date_id_slider
                  let fix_tmp =
                    that.max_scroll_distance - t * (that.add_x_step * 2);
                  e.target.scrollLeft =
                    fix_tmp - e.target.scrollWidth + e.target.clientWidth;
                }
              },
              100,
              tmp
            );
          }
        });
    },
    num2text(num) {
      let result = "";
      if (num === 0) return "0";
      let t = 0;
      while (num > 0) {
        if (t == 3) {
          result += ",";
          t = 0;
        }
        result += num % 10;
        num = parseInt(num / 10);
        t++;
      }
      return result
        .split("")
        .reverse()
        .join("");
    },
    async getCountryDataset(test_url, month_begin = 1, day_begin = 1) {
      let body = {
        args: {
          req: {
            none: ""
          }
        },
        service: "THOuterDataServer",
        func: "getAreaInfo"
      };
      let request = {
        method: "POST",
        headers: [["Content-Type", "application/json"]],
        body: JSON.stringify(body)
      };
      let data = await d3.json(test_url, request);
      let country_data = data.args.rsp;
      let country_dataset = [];
      country_data.chinaHistoryTotal.forEach(d => {
        let day = +d["day"].split(".")[1];
        let month = +d["day"].split(".")[0];
        if (month > month_begin || (month == month_begin && day >= day_begin)) {
          country_dataset.push({
            // total_cure: d.heal,
            total_death: d.dead,
            total_diagnosis: d.confirm,
            // cure_rate: d.heal / d.confirm,
            // death_rate: d.dead / d.confirm,
            month,
            day
          });
        }
      });
      // country_data.modifyHistoryTotal.forEach((d, i) => {
      //   let day = +d["day"].split(".")[1];
      //   let month = +d["day"].split(".")[0];
      //   if (month > month_begin || (month == month_begin && day >= day_begin)) {
      //     country_dataset[i]["cure"] = d.heal;
      //     country_dataset[i]["death"] = d.dead;
      //   }
      // });
      return country_dataset;
    },
    compute_add_y_label() {
      let max_death = 0;
      let max_diagnosis = 0;
      this.country_dataset.forEach(data => {
        if (max_diagnosis < data.total_diagnosis)
          max_diagnosis = data.total_diagnosis;
        if (max_death < data.total_death) max_death = data.total_death;
      });
      // max_death = Math.ceil(max_death / 400) * 400;
      // max_diagnosis = Math.ceil(max_diagnosis / 10000) * 10000;
      // this.add_y_label = [
      //   max_diagnosis,
      //   max_diagnosis / 2,
      //   0,
      //   0,
      //   max_death / 2,
      //   max_death
      // ];
      max_death = 10 ** (Math.ceil(Math.log10(max_death) / 2) * 2)
      max_diagnosis = 10 ** (Math.ceil(Math.log10(max_diagnosis) / 2) * 2)
      // console.log(max_death, max_diagnosis);
      this.add_y_label = [
        max_diagnosis,
        Math.sqrt(max_diagnosis),
        1,
        1,
        // Math.sqrt(max_death),
        max_death
      ];
    },
    set_add_svg_params() {
      let tmp = d3
        .select(".life_svg_div_mortality")
        .node()
        .getBoundingClientRect();
      this.add_svg_width = (tmp.width * this.dataLens) / this.showLens;
      // this.add_svg_width = add_svg_div_rect.width;
      this.add_svg_height = tmp.height;
      this.add_y_step = this.add_svg_height / 5;
      this.add_x_step = this.add_svg_width / 2 / (this.dataLens + 1.6);
      this.add_x_begin = this.add_x_step * 0.8
      this.add_x_end = this.add_svg_width - 2 * this.dataLens * this.add_x_step - this.add_x_begin;
    },
    initData_tencent(isTest = 0) {
      // this.country_dataset = this.getCountryDataset()
      let test_url = "https://ehcard-test.wecity.qq.com/api";
      let formal_url = "https://wechat.wecity.qq.com/api";
      let url = isTest ? test_url : formal_url;
      Promise.all([
        this.getCountryDataset(url)
        // this.getHubeiDataset(url),
        // this.getWuhanDataset(url)
      ]).then(d => {
        // this.dataLens = Math.min(d[0].length, d[1].length, d[2].length);
        // console.log("d[0]", d[0]);
        this.dataLens = Math.min(d[0].length);
        this.showLens = this.dataLens;
        let country_dataset = d[0];
        this.country_dataset = country_dataset;
        let date_list = [];
        this.country_dataset.forEach(d => {
          date_list.push({
            month: d.month,
            day: d.day
          });
        });
        this.date_list = date_list;
        this.select_date_id = date_list.length - 1;
        this.compute_add_y_label();
        // console.log(this.total_y_label)

        this.set_add_svg_params();
        // this.set_add_svg_params();
        this.set_slider();
        // // this.set_scroll_by_jquery();
        this.set_scroll_by_addEvent();
        this.dataViewWidth = d3
          .select(".life_svg_div_mortality")
          .node()
          .getBoundingClientRect().width;
      });
    }
  },
  mounted() {
    this.ScatterPlot.init();
    // let tmp = 0;
    // let timer = setInterval(() => {
    //   this.ScatterPlot.updateView(tmp);
    //   tmp += 1;
    //   if (tmp > 10) {
    //     clearInterval(timer);
    //   }
    // }, 1000);
    this.initData_tencent();
    d3.select(".card-close").on("click", function() {
      d3.select("#data-description").style("display", "none");
    });
    if (/(iPhone|iPad|iPod|iOS|Mac)/i.test(navigator.userAgent)) {
      if (
        /Safari/.test(navigator.userAgent) &&
        /Chrome/.test(navigator.userAgent)
      ) {
        this.isiOS = 0;
      } else {
        // console.log("isIOS");
        this.isiOS = 1;
      }
    }
    let u = navigator.userAgent;
    let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    if (isIOS) {
      this.isiOS = 1;
    }
  }
};
</script>

