export let requestDataCountry = function(api) {
  let requestBody = api['requestBody']
  let requestInit = {
    method: 'POST',
    headers: [
      ['Content-Type', 'application/json']
    ],
    // credentials: 'include',
    body: JSON.stringify(api['requestBody'])
  }
  // console.log('api data for ', provinceName)
  return d3.json(api[dataKey], requestInit)
}

export function requestData(api, provinceName = '湖北') {
  let requestBody = api['requestBody']
  requestBody['args']['req']['area'] = provinceName
  let requestInit = {
    method: 'POST',
    headers: [
      ['Content-Type', 'application/json']
    ],
    // credentials: 'include',
    body: JSON.stringify(api['requestBody'])
  }
  // console.log('api data for ', provinceName)
  return d3.json(api[dataKey], requestInit)
}

